import React, { useState } from "react";
import axios from "axios";
import Modal from "./components/Modal";

const API = process.env.REACT_APP_API_URL || "http://localhost:3000";

export default function AddUser({ onAdded, token, currentUser }) {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [role, setRole] = useState('user');
  const [submitting, setSubmitting] = useState(false);
  const [modalOpen, setModalOpen] = useState(false); // modal to user
  const [noticeOpen, setNoticeOpen] = useState(false); // modal thng bo
  const [modalMsg, setModalMsg] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation c bn
    const trimmedName = name.trim();
    const trimmedEmail = email.trim();
    const emailRegex = /^\S+@\S+\.[\S]+$/;

    if (!trimmedName) {
      alert('Name khng c  trng');
      return;
    }
    if (!emailRegex.test(trimmedEmail)) {
      alert('Email khng hp l');
      return;
    }

    try {
      setSubmitting(true);
      const config = token ? { headers: { Authorization: `Bearer ${token}` } } : {};
  const payload = { name: trimmedName, email: trimmedEmail };
  if (currentUser?.role === 'admin' && role) payload.role = role;
  const response = await axios.post(`${API}/users`, payload, config);
      console.log('Response:', response.data);
      setName("");
      setEmail("");
  setRole('user');
      if (onAdded) onAdded(); // bo parent refresh danh sch
      setModalMsg('Thm user thnh cng!');
      setNoticeOpen(true);
    } catch (err) {
      console.error('Add user error:', err);
      const msg = err.response?.data?.error || err.response?.data?.message || err.message;
      // Bt li trng email (MongoDB E11000)
      if (typeof msg === 'string' && msg.toLowerCase().includes('duplicate')) {
        setModalMsg('Email  tn ti, vui lng dng email khc.');
        setNoticeOpen(true);
      } else {
        setModalMsg(`Thm tht bi: ${msg}`);
        setNoticeOpen(true);
      }
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <>
      <button className="btn small" type="button" onClick={() => setModalOpen(true)}>Thm ngi dng</button>

      <Modal open={modalOpen} title="Thm ngi dng" onClose={() => setModalOpen(false)}>
        <form className="user-form" onSubmit={handleSubmit}>
          <div className="form-group">
            <input className="input" value={name} onChange={e=>setName(e.target.value)} placeholder="H v tn" required />
          </div>
          <div className="form-group">
            <input className="input" value={email} onChange={e=>setEmail(e.target.value)} placeholder="Gmail" type="email" required />
          </div>
          {currentUser?.role === 'admin' && (
            <div className="form-group">
              <select className="input" value={role} onChange={e=>setRole(e.target.value)}>
                <option value="user">User</option>
                <option value="moderator">Moderator</option>
                <option value="admin">Admin</option>
              </select>
            </div>
          )}
          <div style={{display:'flex',justifyContent:'flex-end',gap:8}}>
            <button type="button" className="btn small" onClick={() => setModalOpen(false)}>Hy</button>
            <button className="btn small" type="submit" disabled={submitting}>{submitting ? 'ang thm...' : 'Thm'}</button>
          </div>
        </form>
      </Modal>

      <Modal open={noticeOpen} title="Thng bo" message={modalMsg} onClose={() => setNoticeOpen(false)} />
    </>
  );
}
