import axios from 'axios';

const API = process.env.REACT_APP_API_URL || 'http://localhost:3000';

// Theo di trng thi refresh token  trnh gi nhiu ln
let isRefreshing = false;
let refreshSubscribers = [];

// Thm subscriber vo hng i
function subscribeTokenRefresh(callback) {
  refreshSubscribers.push(callback);
}

// Thc thi tt c callback trong hng i vi token mi
function onRefreshed(token) {
  refreshSubscribers.forEach(callback => callback(token));
  refreshSubscribers = [];
}

// Hm refresh token
async function refreshAuthToken() {
  try {
    console.log(' Bt u refresh token...');
    const refreshToken = localStorage.getItem('refreshToken');
    if (!refreshToken) {
      console.log(' Khng tm thy refresh token trong localStorage');
      throw new Error('No refresh token available');
    }
    console.log(` Gi refresh token c: ${refreshToken.substring(0, 10)}...`);

    const response = await axios.post(`${API}/auth/refresh`, { refreshToken });
    const { token: newAccessToken, refreshToken: newRefreshToken } = response.data;
    console.log(' Nhn token mi t server');

    // Lu tokens mi
    localStorage.setItem('token', newAccessToken);
    if (newRefreshToken) {
      localStorage.setItem('refreshToken', newRefreshToken);
      console.log(`  lu refresh token mi: ${newRefreshToken.substring(0, 10)}...`);
    }
    console.log(' Hon tt refresh token');

    return newAccessToken;
  } catch (error) {
    // Xa ht tokens nu refresh tht bi
    localStorage.removeItem('token');
    localStorage.removeItem('refreshToken');
    localStorage.removeItem('currentUser');
    throw error;
  }
}

// Request interceptor: thm Authorization header t localStorage
axios.interceptors.request.use((config) => {
  try {
    const token = localStorage.getItem('token');
    if (token && !config.headers?.Authorization) {
      config.headers = config.headers || {};
      config.headers.Authorization = `Bearer ${token}`;
    }
  } catch (e) {}
  return config;
}, (error) => Promise.reject(error));

// Response interceptor: x l 401 v refresh token
axios.interceptors.response.use(
  response => response,
  async error => {
    const originalRequest = error.config;
    
    // Nu khng phi li 401 hoc  th refresh ri, reject lun
    if (!error.response || error.response.status !== 401 || originalRequest._retry) {
      return Promise.reject(error);
    }

    originalRequest._retry = true;

    // Nu ang refresh token ri th i kt qu
    if (isRefreshing) {
      try {
        const token = await new Promise((resolve, reject) => {
          subscribeTokenRefresh(token => {
            if (token) resolve(token);
            else reject(new Error('Failed to refresh token'));
          });
        });
        
        originalRequest.headers['Authorization'] = `Bearer ${token}`;
        return axios(originalRequest);
      } catch (err) {
        window.location.href = '/login';
        return Promise.reject(err);
      }
    }

    // Bt u refresh token
    isRefreshing = true;

    try {
      const newToken = await refreshAuthToken();
      originalRequest.headers['Authorization'] = `Bearer ${newToken}`;
      onRefreshed(newToken);
      isRefreshing = false;
      return axios(originalRequest);
    } catch (refreshError) {
      isRefreshing = false;
      onRefreshed(null);
      // Chuyn v trang login nu refresh tht bi
      window.location.href = '/login';
      return Promise.reject(refreshError);
    }
  }
);

export default axios;
